cc.Class({
    extends: cc.Component,

    properties: {
        w: 5,
        h: 3,
        // wl: 300,
        // hl: 200,
        toolLayer: cc.Node,

        buttonLayer: cc.Node,
        defBg: cc.Node,
        chooseSpr: cc.Node,
        dataNode: cc.Node,
        equipNode: cc.Node,
        buyNode: cc.Node,
        pool: cc.Node,
    },

    onLoad () {
        cc.shop = this;
        this.posArr = this.initPosArr();
        let tab = {};
        let atkArr = hjm.atkArr;
        let defArr = hjm.defArr;
        for (let i = atkArr.length - 1; i >= 0; i--) {
            tab[atkArr[i]] = true;
        }
        for (let i = defArr.length - 1; i >= 0; i--) {
            tab[defArr[i]] = true;
        }
        this.toolTab = tab;
        // this.maxShowNum = this.w * this.h;
        // this.pageNum = 0;
        this.setType("atk");
    },


    touchOn (p) {
        let touchNode = p.in(...this.buttonLayer.getChildren());
        if (touchNode) {
            this[touchNode.name + "On"](p);
        }
    },

    chooseTool (node) {
        cc.log("chooseTool");
        if (!node) { //
            this.chooseSpr.x = -2000;
            this.chooseNode = null;
            this.setData("default");
            this.buyNode.lab = "";
        }
        else {
            this.chooseSpr.setPosition(node);
            this.chooseNode = node;
            this.setData(node.name);
            if (this.toolTab[node.name]) {
                this.buyNode.lab = "";
            }
            else {
                this.buyNode.lab = "";   
            }
        }
    },

    setData (name) {
        cc.log("setData");
        dyl.data(this.type + "." + name, this.dataNode);
        this.dataNode.hp = this.dataNode[this.type];
        hjm[name] = this.dataNode.tool;
    },

    atkOn () {
        cc.log("atkOn");
        this.setType("atk");
    },

    defOn () {
        cc.log("defOn");
        this.setType("def");
    },

    nextOn () {
        cc.log("nextOn");
        this.setPage(this.pageNum + 1);
    },

    buyOn () {
        cc.log("buyOn");
        if (this.chooseNode) {
            if (this.toolTab[this.chooseNode.name]) {
                cc.log("");
                this.equip(this.chooseNode.name, this.chooseNode);
                return;
            }
            // if (this.chooseNode.has.active) {
            //     return;
            // }
            else if (this.chooseNode.coin > hjm.coin) {
                return;
            }
            else {
                cc.log("");
                // this.chooseNode.has.active = true;
                this.chooseNode.getChildByName("coin").color = cc.color(0, 255, 0);
                let name = this.chooseNode.name;
                this.toolTab[name] = true;
                hjm[this.type + "Arr"].push(name);
                hjm.coin -= this.chooseNode.coin;
                this.chooseNode.coin = "";
                this.buyNode.lab = "";

                for (let i = this.arr.length - 1; i >= 0; i--) {
                    let node = this.arr[i];
                    if (this.toolTab[node.name]) {
                        continue;
                    }
                    if (node.coin > hjm.coin) {
                        node.getChildByName("coin").color = cc.color(255, 0, 0);
                    }
                }
            }
        }
    },

    chooseOn (p) {
        cc.log("chooseOn");
        let tool = p.in(...this.toolArr);
        if (tool && tool.active) {
            this.chooseTool(tool);
        }
    },

    backOn (p) {
        this.node.add(null, -0.3);
    },

    equip (name, node) {
        hjm[this.type] = name;
        this.equipNode.active = true;
        this.equipNode.setPosition(node);
    },

    setType (type) {
        cc.log("setType");
        this.type = type;
        this.defBg.active = (type === "def");
        this.setArr(hjm[type + "ShopArr"]);
        this.chooseTool();
    },

    setArr (arr) {
        cc.log("setArr");
        this.arr = arr;
        this.setPage(0);
    },

    initTool (node, name) {
        cc.log("initTool");
        dyl.data(this.type + "." + name, node);
        hjm[name] = node.tool;
        node.name = name;
        if (this.toolTab[name]) {
            // node.has.active = true;
            node.coin = "";
            node.getChildByName("coin").color = cc.color(0, 255, 0);
            // node.notMoney.active = false;
        }
        else if (node.coin > hjm.coin) {
            // node.has.active = false;
            // node.notMoney.active = true;
            node.getChildByName("coin").color = cc.color(255, 0, 0);
        }
        else {
            node.getChildByName("coin").color = cc.color(168, 80, 0);
            // node.has.active = false;
            // node.notMoney.active = false;
        }
        if (name === hjm[this.type]) {
            this.equipNode.active = true;
            this.equipNode.setPosition(node);
        }
    },

    setPage (num) {
        cc.log("setPage");
        if (num < 0) {
            return false;
        }
        let maxShowNum = this.w * this.h;  
        let id = maxShowNum * num;
        if (id >= this.arr.length) {
            return false;
        }
        this.equipNode.active = false;
        this.pageNum = num;
        if (id + maxShowNum > this.arr.length) {
            for (let i = id; i < this.arr.length; i++) {
                let node = this.toolArr[i];
                node.active = true;
                this.initTool(node, this.arr[i]);                
            }

            for (let i = this.arr.length; i < id + maxShowNum; i++) {
                this.toolArr[i].active = false;
            }
        }
        else {
            for (let i = id; i < id + maxShowNum; i++) {
                let node = this.toolArr[i];
                node.active = true;
                this.initTool(node, this.toolArr[i]);
            }
        }
        this.chooseTool();
    },

    initPosArr () {
        cc.log("initPosArr");
        let {w, h} = this;
        let size = dyl.getSize(this.toolLayer);
        let wl = size.x / w;
        let hl = size.y / h;
        let oriX = -wl * (w - 1) * 0.5;
        let oriY = hl * (h - 1) * 0.5 + this.toolLayer.y;
        let posArr = [];
        let toolArr = [];
        dyl.addMap(w, h, (p)=>{
            let {x, y} = p;
            let n = y * w + x;
            posArr[n] = cc.p(oriX + x * wl, oriY - y * hl);
            let node = this.pool.add();
            node.setPosition(posArr[n]);
            node.active = false;
            toolArr.push(node);
        });
        this.toolArr = toolArr;
        this.posArr = posArr;
    },

    resetUi () {

    },



});
