cc.Class({
    extends: cc.Component,

    properties: {
        mainDataNode: cc.Node,
        buttonLayer: cc.Node,
        toolData: cc.Node,
    },

    onEnable () {
        this.atkSpr = this.buttonLayer.atk;
        this.defSpr = this.buttonLayer.def;
        let atk = hjm.atk;
        hjm[atk] = this.atkSpr.tool;
        let def = hjm.def;
        hjm[def] = this.defSpr.tool;
        let data = hjm.heroData;
        for (let i in data) {
            this.mainDataNode[i] = data[i];
        }
    },


    touchOn (p) {
        let arr = this.buttonLayer.getChildren();
        let button = p.in(...arr);
        if (button) {
            this[button.name + "On"](p);
        }
    },

    backOn (p) {
        cc.log("data backOn");
        this.node.add(null, -0.3);
    },

    atkOn (p) {
        this.toolData.add(null, 0.3);
        this.toolData.setType("atk");
        this.node.active = false;
    },

    defOn (p) {
        this.toolData.add(null, 0.3);
        this.toolData.setType("def");
        this.node.active = false;
    }

});
