cc.Class({
    extends: cc.Component,

    properties: {
        buttonLayer: cc.Node,
    },

    onLoad: function () {
        this.say = hjm._say.getComponent(cc.Label);

        this.read(hjm.level);
        // setTimeout(()=>{
        //     this.node._say.y = -700;
        // }, 10000);
    },

    touchOn: function (p) {
        let arr = this.buttonLayer.getChildren();
        p.subSelf(this.buttonLayer);
        let button = p.in(...arr);
        if (button) {
            this[button.name + "On"](p);
        }
    },

    shopOn: function (p) {
        cc.log("shopOn");
        hjm._shop.add(null, 0.3);
    },

    stateOn: function (p) {
        cc.log("stateOn");
        hjm._state.add(null, 0.3);
    },

    playOn: function (p) {
        cc.director.loadScene("play");
    },

    read: function (id) {
        // let name = String(id);
        // let data = dyl.data.card[id];
        let data = {};
        dyl.data("card." + String(id), data);
        if (data.type === "shop") {
            this.readShop(data);
        }
        else {
            this.readEn(data);
        }
    },

    readShop: function (data) {
        hjm._enCard.active = false;
        hjm._shopCard.active = true;
        hjm._playOpen.active = true;
        hjm._playFight.active = false;

        this.say.string = data.chName + ": \n        " + data.say;
    },

    readEn: function (data) {
        hjm._enCard.active = true;
        hjm._shopCard.active = false;
        hjm._playOpen.active = false;
        hjm._playFight.active = true;
        // this.node._enCard.add("en/" + data.name);
        this.setSpr(hjm._enCard, "en/" + data.enName);
        this.say.string = data.chName + ": \n        " + data.say;
    },

    setSpr: function (node, path) {
        cc.loader.loadRes(path, cc.SpriteFrame, (err, sprr)=>{
            let sprite = node.getComponent(cc.Sprite);
            sprite.spriteFrame = sprr;
        });
    }
});
