cc.Class({
    extends: cc.Component,

    properties: {
    },

    onLoad () {
        this.node.type = "atk";
        let name = hjm.atk;

        // id name atk atkFun say
        dyl.data("atk." + name, this.node);
        // this.node.atk = Number(dyl.data.atk[name].atk);
        // dyl.atkFun = dyl.data.atk[name].fun;
        // this.node.atk = dyl.data.atk[name];
        // let spr = this.node.getChildByName("spr");
        // cc.loader.loadRes("atk/" + name, cc.SpriteFrame, (err, sprr)=>{
        // 	let sprite = spr.getComponent(cc.Sprite);
        // 	sprite.spriteFrame = sprr;
        // });
        ai.atkFun = this.node.atkFun;
        hjm[name] = this.node.tool;
        let spr = this.node.tool;
        this.node.add = (counter, top, dir, callBack)=>{
        	// cc.log("atk add");
        	spr.parent = top;
        	spr.setPosition(this.node);
        	let time = 0.5;
        	let move = cc.moveBy(time, dir);
        	let fade = cc.fadeTo(time, 50);
        	let scale = cc.scaleTo(time, 0.5);
        	let spawn = cc.spawn(move, fade, scale);
        	let cfun = cc.callFunc(()=>{
        		spr.opacity = 0;
                callBack();
        		counter();
        	});
        	let seq = cc.sequence(spawn, cfun);
        	spr.runAction(seq);
        }

        this.node.reset = ()=>{
        	// cc.log("atk reset");
        	spr.parent = this.node;
        	spr.setPosition(0, 0);
        	spr.opacity = 255;
        	spr.setScale(1);
        	// spr.active = true;
        }
    },



});
