cc.Class({
    extends: cc.Component,

    properties: {
    },

    onLoad () {
        let self = this;
        // this.node.add = (end, skillName, ...arr)=>{
        this.node.add = (end, skillName, tp, map, posMap)=>{
            cc.log("skillName", skillName);
            // cc.log("type", tp, map.get(tp), map.get(tp, "type"));
            if (map.get(tp, "type") === "not") {
                // cc.log("");
                end();
                return;
            }
            if (typeof self[skillName] !== "function") {
                cc.warn("", skillName);
                end();
            }
            this.tp = tp;
            this.map = map;
            this.posMap = posMap;
            self[skillName](end, tp, map, posMap);
        }
    },

    fun_getQueue () {
        let arr = [];
        let id = 0;
        let queue = {};
        queue.push = function (value) {
            arr.push(value);
        }
        queue.pop = function () {
            if (id >= arr.length) {
                return null;
            }
            return arr[id++];
        }
        return queue;
    },

    fun_getDirArr () {
        let {tp, map} = this;
        let atkNode = map.get(tp);
        let enType = (atkNode.type === "me") ? "en" : "me";
        let dirArr = dyl.addDirArr();
        let arr = [];
        for (let i = 0; i < 4; i++) {
            let np = dirArr[i].add(tp);
            if (map.get(np, "type") === enType) {
                arr.push(dirArr[i]);
            }
        }
        return arr;
    },

    act_notFront (np, dir, f = 1) {
        let {tp, map} = this;
        let nowNode = map.get(tp);
        // let dir = np.sub(tp);
        let back = cc.moveBy(0.3, dir.mul(-100 * f));
        back.node = nowNode;
        back.add = 0.6;

        let go = cc.moveBy(0.05, dir.mul(120 * f));
        go.node = nowNode;
        go.add = 1.5;
        return [back, go];
    },

    act_notBack (np, dir, f = 1) {
        let {tp, map, posMap} = this;
        let nowNode = map.get(tp);
        let nextNode = map.get(np);
        // let dir = np.sub(tp);
        let back1 = cc.moveBy(0.1, dir.mul(-20 * f));
        back1.node = nowNode;
        let back2 = cc.moveBy(0.1, dir.mul(30 * f));
        back2.node = nextNode;
        back2.add = 0.6;
        let reset1 = cc.moveTo(0.05, posMap.get(tp));
        reset1.node = nowNode;
        let reset2 = cc.moveTo(0.05, posMap.get(np));
        reset2.node = nextNode;
        return [[back1, back2], [reset1, reset2], 0.3];
    },

    act_arrBack (arr, dir, f = 1) {
        let arr0 = [];
        let arr1 = [];
        for (let i = arr.length - 1; i >= 0; i--) {
            let notBackArr = this.act_notBack(arr[i], dir, f);
            arr0.push(...notBackArr[0]);
            arr1.push(...notBackArr[1]);
        }
        return [arr0, arr1, 0.3];
    },

    act_rotateArrBack (dirArr) {
        let {tp, map, posMap} = this;

        let backArr1 = [];
        let backArr2 = [];
        for (let i = dirArr.length - 1; i >= 0; i--) {
            let dir = dirArr[i];
            let np = dir.add(tp);
            let move1 = cc.moveBy(0.1, dir.mul(30));
            move1.node = map.get(np);
            backArr1.push(move1);

            let move2 = cc.moveTo(0.05, posMap.get(np));
            move2.node = map.get(np);
            backArr2.push(move2);
        }
        return [0.2, backArr1, backArr2];
    },

    act_rotate (np) {
        let {map} = this;
        let node = map.get(np);
        node.setScale(1.2);
        let rotate = cc.rotateTo(0.35, 720);
        rotate.node = map.get(np);
        return [rotate, ()=>node.setScale(1)];
    },

    fun_rotate (dirArr) {
        let {tp, map} = this;
        for (let i = dirArr.length - 1; i >= 0; i--) {
            this.fun_atk(dirArr[i].add(tp));
        }
    },

    fun_atk (np, r = 1) {
        // cc.log("notAtk", atkNode.atk, defNode.def);
        let {tp, map} = this;
        let atkNode = this.map.get(tp);
        let defNode = this.map.get(np);
        let hurtNum = 0;
        if (atkNode.atk * r > defNode.def) {
            hurtNum = atkNode.atk * r - defNode.def;
        }
        // hurtNum *= r;
        hurtNum = Math.floor(hurtNum);
        let defFunName = defNode.defFun;
        // cc.log("defFunName--------", defFunName, "ddd");
        if (this[defFunName]) {
            this[defFunName](hurtNum, atkNode, defNode);
        }
        else {
            this.notDef(hurtNum, atkNode, defNode);
        }
    },

    fun_arrAtk (arr, r = 1) {
        for (let i = arr.length - 1; i >= 0; i--) {
            this.fun_atk(arr[i], r);
        }
    },

    fun_getLinkArr (np) {
        let {map} = this;
        let type = map.get(np, "type");
        let visit = dyl.addMap(map.w, map.h, ()=>false);
        let queue = this.fun_getQueue();
        queue.push(np);
        let ans = [np];
        visit.set(np, true);
        while(true) {
            let now = queue.pop();
            if (!now) {
                break;
            }
            let dirArr = dyl.addDirArr();
            for (let i = 0; i < 4; i++) {
                let p = now.add(dirArr[i]);
                if (map.get(p, "type") === type && !visit.get(p)) {
                    visit.set(p, true);
                    ans.push(p);
                    queue.push(p);
                }
            }
        }
        return ans;
    },

    notBackDef (hurtNum, atkNode, defNode) {
        cc.log("notBackDef");
        hurtNum = Math.floor(hurtNum);
        defNode.hp -= hurtNum;
        atkNode.hp -= 1;
        hjm._hpLayer.add(defNode, -hurtNum);
        hjm._hpLayer.add(atkNode, -1);
    },

    notDef (hurtNum, atkNode, defNode) {
        cc.log("notDef");
        hurtNum = Math.floor(hurtNum);
        defNode.hp -= hurtNum;
        hjm._hpLayer.add(defNode, -hurtNum);
    },

    notAtk (end) {
        let dir = this.fun_getDirArr()[0];
        if (!dir) {
            return end();
        }
        let np = dir.add(this.tp);
        let atkFun = ()=>this.fun_atk(np);
        let arr = [...this.act_notFront(np, dir), atkFun, ...this.act_notBack(np, dir)];
        dyl.run(this.node, ...arr, end);
    },

    notState (end) {
        return end();
    },

    friendState (end) {
        let {tp, map} = this;
        let node = map.get(tp);
        let type = node.type;
        let dirArr = dyl.addDirArr();
        // node.atk--;
        let atk = node.oriData.atk;
        for (let i = 0; i < 4; i++) {
            let np = dirArr[i].add(tp);
            if (map.get(np, "type") === type) {
                atk += 1;
            }
        }
        node.atk = atk;
        end();
    },

    bigAtk (end) {
        let {tp, map} = this;
        if (dyl.rand() > 0.5) {
            return this.notAtk(end);
        }
        // cc.log("bigAtk", tp);
        let dir = this.fun_getDirArr()[0];
        if (!dir) {
            return end();
        }
        let np = dir.add(this.tp);
        // cc.log("big");
        let scale1 = cc.scaleTo(0.1, 1.2);
        scale1.node = map.get(tp);
        let scale2 = cc.scaleTo(0.1, 1);
        scale2.node = map.get(tp);
        // let arr = [scale1, ...this.notAction1(tp, np, map, 1.5), ...this.notAction2(tp, np, map, posMap, 1.5), scale2];

        let atkFun = ()=>this.fun_atk(np, 2);
        let arr = [scale1, ...this.act_notFront(np, dir, 1.5), atkFun, ...this.act_notBack(np, dir, 1.5), scale2];

        dyl.run(this.node, ...arr, end);
    },

    twoAtk (end) {
        let tmpEnd = ()=>{
            this.notAtk(end);
        }  
        this.notAtk(tmpEnd);
    },

    electricAtk (end) {
        // cc.log("electricAtk");
        let dir = this.fun_getDirArr()[0];
        if (!dir) {
            return end();
        }

        let arr = this.fun_getLinkArr(this.tp.add(dir));


        let atkFun = ()=>this.fun_arrAtk(arr);
        let seqArr = [...this.act_notFront(dir.mul(this.tp), dir), atkFun, ...this.act_arrBack(arr, dir)];
        dyl.run(this.node, ...seqArr, end);
    },

    rotateAtk (end) {
        let dirArr = this.fun_getDirArr();
        if (dirArr.length < 1) {
            return end();
        }

        let callId = 0;
        let callBack = ()=>{
            callId++;
            if (callId === 3) {
                end();
            }
        }

        let rotateArr = this.act_rotateArrBack(dirArr);
        let rotate = this.act_rotate(this.tp);
        let fun = ()=>this.fun_rotate(dirArr);
        dyl.run(...rotateArr, callBack);
        dyl.run(...rotate, callBack);
        dyl.run(0.25, fun, callBack);

        // let np = dir.add(this.tp);
        // let atkFun = ()=>this.fun_atk(np);
        // let arr = [...this.act_notFront(np, dir), atkFun, ...this.act_notBack(np, dir)];
        // dyl.run(this.node, ...arr, end);
    },
});
