cc.Class({
    extends: cc.Component,

    properties: {
        w: 5,
        h: 3,
        // wl: 300,
        // hl: 200,
        toolLayer: cc.Node,

        buttonLayer: cc.Node,
        // defBg: cc.Node,
        chooseSpr: cc.Node,
        dataNode: cc.Node,
        pool: cc.Node,
        stateNode: cc.Node,
    },

    onLoad () {
        this.posArr = this.initPosArr();
        // let tab = {};
        // let atkArr = hjm.atkArr;
        // let defArr = hjm.defArr;
        // for (let i = atkArr.length - 1; i >= 0; i--) {
        //     tab[atkArr[i]] = true;
        // }
        // for (let i = defArr.length - 1; i >= 0; i--) {
        //     tab[defArr[i]] = true;
        // }
        // this.toolTab = tab;
        // this.maxShowNum = this.w * this.h;
        // this.pageNum = 0;
        // this.setType("atk");
        this.node.setType = (type)=>this.setType(type);


    },


    touchOn (p) {
        let touchNode = p.in(...this.buttonLayer.getChildren());
        if (touchNode) {
            this[touchNode.name + "On"](p);
        }
    },

    chooseTool (node) {
        cc.log("chooseTool");
        // if (!node) { //
        //     this.chooseSpr.x = -2000;
        //     this.chooseNode = null;
        //     this.setData("default");
        // }
        // else {
            this.chooseSpr.setPosition(node);
            this.chooseNode = node;
            this.setData(node.name);
            hjm[this.type] = node.name;
        // }
    },

    setData (name) {
        cc.log("setData");
        dyl.data(this.type + "." + name, this.dataNode);
        this.dataNode.hp = this.dataNode[this.type];
        hjm[name] = this.dataNode.tool;
    },

    // atkOn () {
    //     cc.log("atkOn");
    //     this.setType("atk");
    // },

    // defOn () {
    //     cc.log("defOn");
    //     this.setType("def");
    // },

    nextOn () {
        cc.log("nextOn");
        this.setPage(this.pageNum + 1);
    },

    // buyOn () {
    //     cc.log("buyOn");
    //     if (this.chooseNode) {
    //         if (this.chooseNode.has.active) {
    //             return;
    //         }
    //         else if (this.chooseNode.notMoney.active) {
    //             return;
    //         }
    //         else {
    //             this.chooseNode.has.active = true;
    //             let name = this.chooseNode.name;
    //             this.toolTab[name] = true;
    //             hjm[this.type + "Arr"].push(name);
    //             hjm.coin -= this.chooseNode.coin;
    //         }
    //     }
    // },

    chooseOn (p) {
        cc.log("chooseOn");
        let tool = p.in(...this.toolArr);
        if (tool && tool.active) {
            this.chooseTool(tool);
        }
    },

    backOn (p) {
        this.node.add(null, -0.3);
        this.stateNode.active = true;
    },

    setType (type) {
        cc.log("setType");
        this.type = type;
        this.setArr(hjm[type + "Arr"]);
        // this.chooseTool();
    },

    setArr (arr) {
        cc.log("setArr");
        this.arr = arr;
        this.setPage(0);
    },

    initTool (node, name) {
        cc.log("initTool");
        dyl.data(this.type + "." + name, node);
        hjm[name] = node.tool;
        node.name = name;
        // if (this.toolTab[name]) {
        //     node.has.active = true;
        //     node.notMoney.active = false;
        // }
        // else if (node.coin > hjm.coin) {
        //     node.has.active = false;
        //     node.notMoney.active = true;
        // }
        // else {
        //     node.has.active = false;
        //     node.notMoney.active = false;
        // }
        if (name === hjm[this.type]) {
            this.chooseNode = node;
        }

    },

    setPage (num) {
        cc.log("setPage");
        if (num < 0) {
            return false;
        }
        let maxShowNum = this.w * this.h;  
        let id = maxShowNum * num;
        if (id >= this.arr.length) {
            return false;
        }
        this.pageNum = num;
        if (id + maxShowNum > this.arr.length) {
            for (let i = id; i < this.arr.length; i++) {
                let node = this.toolArr[i];
                node.active = true;
                this.initTool(node, this.arr[i]);                
            }

            for (let i = this.arr.length; i < id + maxShowNum; i++) {
                this.toolArr[i].active = false;
            }
        }
        else {
            for (let i = id; i < id + maxShowNum; i++) {
                let node = this.toolArr[i];
                node.active = true;
                this.initTool(node, this.toolArr[i]);
            }
        }
        this.chooseTool(this.chooseNode);
    },

    initPosArr () {
        cc.log("initPosArr");
        let {w, h} = this;
        let size = dyl.getSize(this.toolLayer);
        let wl = size.x / w;
        let hl = size.y / h;
        let oriX = -wl * (w - 1) * 0.5;
        let oriY = hl * (h - 1) * 0.5 + this.toolLayer.y;
        let posArr = [];
        let toolArr = [];
        dyl.addMap(w, h, (p)=>{
            let {x, y} = p;
            let n = y * w + x;
            posArr[n] = cc.p(oriX + x * wl, oriY - y * hl);
            let node = this.pool.add();
            node.setPosition(posArr[n]);
            node.active = false;
            toolArr.push(node);
        });
        this.toolArr = toolArr;
        this.posArr = posArr;
    },

    resetUi () {

    },



});
