cc.Class({
    extends: cc.Component,

    properties: {
    },

    onLoad () {
        this.node.type = "def";
        let name = hjm.def;
        // this.node.atk = dyl.data.atk[name];
        // this.node.def = Number(dyl.data.def[name].def);
        // dyl.defFun = dyl.data.def[name].fun;
        dyl.data("def." + name, this.node);
        ai.defFun = this.node.defFun;
        hjm[name] = this.node.tool;
        // let spr = this.node.getChildByName("spr");
        // cc.log("spr", spr);
        // cc.loader.loadRes("def/" + name, cc.SpriteFrame, (err, sprr)=>{
        // 	let sprite = spr.getComponent(cc.Sprite);
        // 	sprite.spriteFrame = sprr;
        // 	// cc.log("end");
        // });
        let spr = this.node.tool;
        this.node.add = (end, top, dir, callBack)=>{
        	// cc.log("def add");
        	spr.parent = top;
        	spr.setPosition(this.node);
        	let time = 0.5;
        	let move = cc.moveBy(time, dir);
        	let fade = cc.fadeTo(time, 50);
        	let scale = cc.scaleTo(time, 0.5);
        	let spawn = cc.spawn(move, fade, scale);
        	let cfun = cc.callFunc(()=>{
        		spr.opacity = 0;
                callBack();
        		end();
        	});
        	let seq = cc.sequence(spawn, cfun);
        	spr.runAction(seq);
        }

        this.node.reset = ()=>{
        	// cc.log("def reset");
        	spr.parent = this.node;
        	spr.setPosition(0, 0);
        	spr.opacity = 255;
        	spr.setScale(1);
        	// spr.active = true;
        }
    },

});
