cc.Class({
    extends: cc.Component,

    properties: {
    },

    onLoad () {
        // dyl.atk = "crossed-swords";
        // dyl.def = "templar-shield";
        this.node.touch = "stop";

        let lv = hjm.level;
        // let randNum = Number(dyl.data.levelData.rand);
        let randNum = dyl.data("levelData." + String(lv), this).rand;
        // this.node.rand.set(randNum);
        dyl.setRand(randNum);
        // cc.log("rand", dyl.rand(100));
        let enNameArr = this.getEnNameArr(lv);
        this.getEnArr(enNameArr);
    },

    getEnNameArr (lv) {
        // let data = dyl.data.levelData[lv]; 
        let data = {};
        dyl.data("levelData." + String(lv), data);
        let enNameArr = [];
        for (let i = 0; i < 5; i++) {
            let name = data["en" + String(i)];
            if (name && (name !== "")) {
                enNameArr.push("role/" + name);
            }
        }
        // cc.log(enNameArr);
        return enNameArr;
    },

    getEnArr (enNameArr) {
        let count = enNameArr.length;
        let enArr = [];
        let fun = ()=>{
            count--;
            if (!count) {
                this.initMap(enArr);
                this.node.getComponent("GameMain").myStart();
            }
        }
        for (var i = enNameArr.length - 1; i >= 0; i--) {
            let name = enNameArr[i];
            cc.loader.loadRes(name, (err, prefab)=>{
                let node = cc.instantiate(prefab);
                enArr.push(node);
                // cc.log(name, err);
                node.parent = hjm._en;
                fun();
            });
        }
    },

    getNotArr (notNum) {
        let notArr = [];
        let nodeArr = hjm._not.getChildren();
        for (let i = 0; i < notNum; i++) {
            let node = nodeArr[i];
            node.active = true;
            notArr.push(node);
        }
        this.node.notArr = [];
        for (let i = notNum; i < nodeArr.length; i++) {
            this.node.notArr.push(nodeArr[i]);
        }
        return notArr;
    },

    initMap (enArr) {
        let myArr = [...hjm._me.getChildren()];
        let notNum = 8 - myArr.length - enArr.length;
        let notArr = this.getNotArr(notNum);
        let nodeArr = [0, ...myArr, ...notArr, ...enArr];
        nodeArr.sort(()=>{
            let tmp = dyl.rand() - 0.5;
            // cc.log("tmp", tmp);
            return tmp;
        });

        let id = 0; 
        let posMap = dyl.addMapLayer(3, 3, 315);
        //checkIn(p,isT) tposToPos(tp) getTpos(p) getPos(p, isT)
        let map = dyl.addMap(3, 3, (p)=>{
            let i = id++;
            let node = nodeArr[i];
            if (node) {
                node.setPosition(posMap.get(p));
                return nodeArr[i];
            }
            return null;
        });
        this.node.posMap = posMap;
        this.node.map = map;
    },
    // update (dt) {},
});
