cc.Class({
    extends: cc.Component,

    properties: {
    },

    onLoad () {
    	this.node.add = (path)=>{
    		cc.loader.loadRes(path, cc.SpriteFrame, (err, sprr)=>{
        		let sprite = this.node.getComponent(cc.Sprite);
        		sprite.spriteFrame = sprr;
        	});
    	}
    },

});
