cc.Class({
    extends: cc.Component,

    properties: {
    },

    start () {
        this.node.js = this;
        this.touchData = null;
    },

    saveTouch (tp) {
        if (!this.touchData) {
            this.touchData = "ing";
            this.play(tp);
        }
        else if (this.touchData === "ing") {
            this.touchData = tp;
        }
        else {
            this.touchData = tp;
        }
    },

    delTouch () {
        if (!this.touchData) {
        }
        else if (this.touchData === "ing") {
            this.touchData = null;
        }
        else {
            // this.touchData = tp;
            let tp = this.touchData;
            this.touchData = "ing";
            this.play(tp);
        }
    },

    getAction () {
        let id = 0;
        let dataArr = [];        
        let end = ()=>{
            let i = id++;
            if (i >= dataArr.length) {
                return;
            }
            let {node, fun, arr} = dataArr[i];
            fun.bind(node)(end, ...arr);
        }

        let add = (node, fun, ...arr)=>{
            if (!node) {
                return end();
            }
            if (!fun) {
                return;
            }
            let data = {
                node: node,
                fun: fun,
                arr: arr
            }
            dataArr.push(data);
        }
        return add;
    },

    touchOn (p) {
        let tp = this.node.posMap.find(p);
        if (tp) {
            this.saveTouch(tp);
        }
    },

    play (tp) {
        let map = this.node.map;
        let dir = this.getDir(tp);      
        if (!dir) { //
            this.delTouch();
            return;
        }  
        let moveNode = map.get(tp);
        this.moveType = moveNode.type;
        moveNode.oriParent = moveNode.parent;
        moveNode.parent = hjm._top;
        let add = this.getAction();
        add(this, this.run, tp, dir);
        add(this, this.prePk);
        if (this.moveType === "me") {
            // this.isAtk = 
            // cc.log("me");
            let enPosArr = this.getAtkDefEn(tp.add(dir));
            moveNode.defFun = this.defFun;
            if (this.atkNode && enPosArr.length > 0) {
                // cc.log("1111111111111111111");
                add(this.atkNode, this.atkNode.add, hjm._top, this.atkDir);
                add(this, (end)=>{
                    // cc.log(typeof this.atkNode.atk, typeof moveNode.atk);
                    moveNode.atk += this.atkNode.atk;
                    end();
                });
                // cc.log("atk");
            }
            if (this.defNode && enPosArr.length > 0) {
                // cc.log("2222222222222222");
                add(this.defNode, this.defNode.add, hjm._top, this.defDir);
                add(this, (end)=>{
                    moveNode.def += this.defNode.def;
                    end();
                });
            }
            cc.log("this.atkFun", this.atkFun);
            add(this, hjm._skill.add, this.atkFun, tp, map, this.node.posMap);
            add(this, this.checkDie);
            for (let i = enPosArr.length - 1; i >= 0; i--) {
                let ep = enPosArr[i];
                let en = map.get(ep);
                add(this, hjm._skill.add, en.atkFun, ep, map, this.node.posMap);
                add(this, this.checkDie);
            }
            // if (this.isAtk) {
            //     add(this, this.node._skill.add, dyl.atkFun, tp, map, this.node.posMap);
            // }
            // else {
            //     add(this, this.node._skill.add, "notAtk", tp, map, this.node.posMap);
            // }
        }
        else if (this.moveType === "en") {
            add(this, hjm._skill.add, moveNode.atkFun, tp, map, this.node.posMap);
            add(this, this.checkDie);
        }

        add(this, this.endPlay, tp);
        add(); 
    },

    checkDie (end) {
        let notLose = false;
        let notWin = false;
        let map = this.node.map;
        map.run((tp)=>{
            let node = map.get(tp);
            if (node && node.hp <= 0) {
                this.die(tp);
                return;
            }
            if (node && node.type === "me") {
                notLose = true;
            }
            if (node && node.type === "en") {
                notWin = true;
            }
        })
        if (!notLose) {
            this.endAns = "lose";
            end();
            return;
        }
        if (!notWin) {
            this.endAns = "win";
            end();
            return;
        }
        end();
        return;
    },

    die (tp) {
        let notArr = this.node.notArr;
        let not = notArr[notArr.length - 1];
        notArr.length--;
        not.active = true;

        let node = this.node.map.get(tp);
        node.active = false;
        not.setPosition(this.node.posMap.get(tp));
        this.node.map.set(tp, not);
    },

    getAtkDefEn (tp) {
        let map = this.node.map;
        let dirArr = dyl.addDirArr();
        let enPosArr = [];
        this.atkFun = "notAtk";
        this.defFun = "notDef";
        this.atkNode = null;
        this.defNode = null;
        for (let i = 0; i < 4; i++) {
            let dir = dirArr[i];
            let ntp = dir.add(tp);
            let type = map.get(ntp, "type");
            if (type === "atk") {
                this.atkFun = ai.atkFun;
                this.atkNode = map.get(ntp);
                this.atkDir = dir.mul(-this.node.posMap.d);
            }
            else if (type === "def") {
                this.defFun = ai.defFun;
                this.defNode = map.get(ntp);
                this.defDir = dir.mul(-this.node.posMap.d);
            }
            else if (type === "en") {
                enPosArr.push(ntp);
            }
        }
        return enPosArr;
    },

    prePk (end) {
        let map = this.node.map;
        map.run((p)=>{
            let node = map.get(p);
            if (node && (node.type !== "not")) {
                node.oriData = {
                    // hp: node.hp,
                    atk: node.atk,
                    def: node.def
                }
            }
        })
        this.isAtk = "notAtk";
        this.isDef = "notDef";
        end();
    },




    // saveTouch (tp) {
    //     if (!this.touchTp) {
    //         this.play(tp);
    //     }
    // }, 

    endPlay (end, tp) {
        cc.log("endPlay");
        let moveNode = this.node.map.get(tp);
        moveNode.parent = moveNode.oriParent;
        let map = this.node.map;
        map.run((p)=>{
            let node = map.get(p);
            if (node && (node.type !== "not")) {
                // node.hp = node.oriData.hp;
                node.atk = node.oriData.atk;
                node.def = node.oriData.def;
            }
        })
        if (this.atkNode) {
            this.atkNode.reset();
        }
        if (this.defNode) {
            this.defNode.reset();
        }
        // this.checkDie();
        this.delTouch();
        if (this.endAns) {
            this.node.touch = "end";
            hjm._end.add(this.endAns, 0.3);
        }
        end();
    },

    run (end, tp, dir) {
        let ntp = dir.add(tp);
        let move = cc.moveTo(0.2, this.node.posMap.get(ntp));
        // move.add = -3;
        let node = this.node.map.get(tp);
        dyl.run(node, move, ()=>{
            this.node.map.set(tp, null);
            this.node.map.set(ntp, node);
            tp.x = ntp.x;
            tp.y = ntp.y;
        }, end);
    },

    getDir (tp) {
        let map = this.node.map;
        if (!map.get(tp)) {
            return null;
        }
        let dirArr = dyl.addDirArr();
        for (let i = 0; i < 4; i++) {
            let dir = dirArr[i];
            let ntp = dir.add(tp);
            if (map.get(ntp) === null) {
                return dir;
            }
        }
        return null;
    },
    // update (dt) {},
});
